.film-gallery {
  background-color: $bg-modal;
}

.film-gallery__list,
.library__list {
  display: flex;

  margin: 20px 0 40px;

  @include mq(mobile-only) {
    gap: 20px;
    flex-direction: column;
  }

  @include mq(tablet-only) {
    flex-wrap: wrap;
    gap: 32px;
  }

  @include mq(desktop) {
    flex-wrap: wrap;
  }
}

.film-gallery__item {
  @include mq(mobile-only) {
    width: 280px;
  }

  @include mq(tablet-only) {
    width: 336px;
  }

  @include mq(desktop) {
    width: 395px;
    margin-right: 15px;
    margin-bottom: 32px;

    &:nth-child(3n) {
      margin-right: 0;
    }

    &:nth-last-child(-n + 3) {
      margin-bottom: 0;
    }
  }
}

.film-gallery__img {
  object-fit: cover;
  cursor: pointer;

  @include mq(mobile-only) {
    height: 402px;
    width: 280px;
  }

  @include mq(tablet-only) {
    height: 455px;
    width: 336px;
  }

  @include mq(desktop) {
    height: 574px;
    width: 395px;
  }
}

.film {
  @include mq(mobile-only) {
    margin-top: 10px;
    word-break: break-word;
  }

  @include mq(tablet-only) {
    margin-top: 8px;
    word-break: break-word;
  }

  @include mq(desktop) {
    margin-top: 12px;
    word-break: break-word;
  }
}

.film__wrapper {
  display: flex;
  gap: 5px;
  align-items: baseline;
}

.film__title,
.library__title {
  @include font(500, 12px, 1.17, null);
  color: $text-color-primary;
  text-transform: uppercase;

  @include mq(tablet-only) {
    @include font(500, 12px, 1.33, null);
  }

  @include mq(desktop) {
    @include font(500, 20px, 1.2, null);
  }
}

.darkmode--activated .film__title {
  color: $text-color-secondary;
}

.film__wrapper-reset {
  display: inline-block;
  @include font(500, 12px, 1.33, null);
  color: $text-color-secondary;

  @include mq(tablet) {
    @include font(500, 12px, 1.33, null);
  }

  @include mq(desktop) {
    @include font(500, 20px, 1.2, null);
  }
}

.card {
  transition: 0.5s linear;
  border-radius: 5px;
  &:hover,
  &:focus {
    transform: scale(1.05);
    box-shadow: 4px 3px 12px -1px rgba(255, 107, 8, 0.5);
  }
}

.film__rating {
  display: flex;
  justify-content: center;

  width: 36px;
  height: 16px;
  border-radius: 5px;

  @include font(500, 12px, 1.17, null);
  background-color: $text-color-secondary;
  color: $text-color-lighter;

  @include mq(desktop) {
    width: 40px;
    height: 20px;
    align-items: center;
  }
}
